'use client';
import { useState } from 'react';

export default function CreateTournament() {
  const [name, setName] = useState('');
  const [system, setSystem] = useState('Round Robin');
  const [lockSystem, setLockSystem] = useState(false);
  const [time, setTime] = useState(10);
  const [date, setDate] = useState('');
  const [tournaments, setTournaments] = useState<any[]>([]);

  const handleSubmit = (e: any) => {
    e.preventDefault();
    const newTournament = { name, system, lockSystem, time, date, id: Date.now() };
    setTournaments([...tournaments, newTournament]);
    localStorage.setItem('tournaments', JSON.stringify([...tournaments, newTournament]));
    alert(`Torneo "${name}" creado!`);
  };

  return (
    <div className="space-y-6">
      <form className="max-w-xl mx-auto p-6 bg-white rounded shadow space-y-4" onSubmit={handleSubmit}>
        <h2 className="text-2xl font-bold">Crear Torneo</h2>
        <div>
          <label>Nombre del torneo:</label>
          <input type="text" value={name} onChange={(e) => setName(e.target.value)}
                 className="border p-2 w-full rounded" required />
        </div>
        <div>
          <label>Sistema:</label>
          <select value={system} onChange={(e) => setSystem(e.target.value)} disabled={lockSystem}
                  className="border p-2 w-full rounded">
            <option>Round Robin</option>
            <option>Suizo</option>
            <option>Eliminaci√≥n</option>
          </select>
          <label className="ml-2">
            <input type="checkbox" checked={lockSystem} onChange={(e) => setLockSystem(e.target.checked)} />
            Solo Sistema por Emparejamiento
          </label>
        </div>
        <div>
          <label>Tiempo (minutos):</label>
          <input type="number" value={time} onChange={(e) => setTime(Number(e.target.value))}
                 className="border p-2 w-full rounded" min={1} max={120} />
        </div>
        <div>
          <label>Fecha y Hora:</label>
          <input type="datetime-local" value={date} onChange={(e) => setDate(e.target.value)}
                 className="border p-2 w-full rounded"
                 min={new Date().toISOString().slice(0,16)} />
        </div>
        <button type="submit" className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700">
          Crear Torneo
        </button>
      </form>
    </div>
  );
}

